export class CodeRuleBaseInfo{
    id:string;
    code:string;
    name:string;
    serviceUnit:string;
    origin:string;

    init(obj:Object){
        this.id=obj["id"];
        this.code=obj["code"];
        this.name=obj["name"];
        this.serviceUnit=obj["serviceUnit"];
        this.origin=obj["origin"];
    }
}