import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { CodeRuleBaseInfo } from '../model/code-rule-base-info';

@Injectable()
export class CodeRuleSelectService {

  constructor(private http: HttpClient) {
  }

  public serviceUnit: string;
  public sessionId: string;
  private QUERY_URL: string = "/api/runtime/bcc/v1.0/code-rule-data"

  codeRule: CodeRuleBaseInfo;

  metadatas = [];

  addMetadataRT(metadata) {
    this.metadatas.push(metadata);
  }

  clearData() {
    this.metadatas = [];
    this.codeRule = null;
  }

  // 根据类型获取Cloud Server下所有的元数据
  getCodeRuleList(): Observable<any> {
    let headers:HttpHeaders = new HttpHeaders();
    headers.append("sessionId", this.sessionId);
    return this.http.get(`${this.QUERY_URL}?serviceUnit=${this.serviceUnit}`, { headers: headers });
  }



}
