// tslint:disable-next-line:max-line-length
import { Component, ViewChild, Input, OnChanges, Output, EventEmitter, SimpleChanges, ComponentFactoryResolver, Injector } from '@angular/core';
import { CodeRuleSelectService } from '../../service/code-rule-select.service';
import { CodeRuleInvokeInfo } from '../../model/code-rule-invoke-info';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-select-metadatart',
  templateUrl: './select-code-rule.component.html',
  styleUrls: ['./select-code-rule.component.css']
})
export class SelectCodeRuleComponent implements OnChanges {
  @Input() set serviceUnit(value: string) {
    this.codeRuleSvc.serviceUnit = value;
  }
  @Input() set sessionId(value: string) {
    this.codeRuleSvc.sessionId = value;
  }
  @Output() selectorClosed: EventEmitter<any>;
  @Output() codeRuleSelected = new EventEmitter<CodeRuleInvokeInfo>();

  constructor(private codeRuleSvc: CodeRuleSelectService,
    private resolver: ComponentFactoryResolver, private injector: Injector) {
    this.selectorClosed = new EventEmitter<any>();
  }

  ngOnChanges(changes: SimpleChanges): void {

  }

  /**
   * 点击确认，确认编号规则
   */
  confirmCodeRule() {
    const codeRuleInfo = this.codeRuleSvc.codeRule;
    if (!codeRuleInfo) {
      alert("当前未选中任何数据！");
      return;
    }
    const invokeInfo: CodeRuleInvokeInfo = new CodeRuleInvokeInfo();
    invokeInfo.id = codeRuleInfo.id;
    invokeInfo.code = codeRuleInfo.code;
    invokeInfo.name = codeRuleInfo.name;
    invokeInfo.serviceUnit = codeRuleInfo.serviceUnit;
    invokeInfo.byMetadata = codeRuleInfo.origin == "元数据" ? true : false;
    this.codeRuleSelected.emit(invokeInfo);
    this.closeSelector();
    // 清空缓存
    this.codeRuleSvc.clearData();
  }

  /**
   * 点击取消，关闭选择器
   */
  closeSelector(): void {
    this.selectorClosed.emit();
  }

}
