import { Component, Input, OnInit } from '@angular/core';
import { process, State } from '@progress/kendo-data-query';
import { GridDataResult, DataStateChangeEvent,SelectableSettings} from '@progress/kendo-angular-grid';
import { CodeRuleBaseInfo } from '../../model/code-rule-base-info';
import { CodeRuleSelectService } from '../../service/code-rule-select.service';


@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-coderule-list',
  templateUrl: './code-rule-list.component.html',
  styleUrls: ['./code-rule-list.component.css']
})
export class CodeRuleListComponent implements OnInit {

  constructor(private codeRuleSvc: CodeRuleSelectService) { }

  data: CodeRuleBaseInfo[]=new Array<CodeRuleBaseInfo>();
  clear() {
    this.data = [];
  }

  // tslint:disable-next-line:member-ordering
  public state: State = {
    skip: 0,
    take: 9999,
    // Initial filter descriptor
    filter: {
      logic: 'and',
      filters: []
    }
  };

  // tslint:disable-next-line:member-ordering
  public gridData: GridDataResult;
  public dataStateChange(state: DataStateChangeEvent): void {
    this.state = state;
    this.gridData = process(this.data, this.state);
  }

  // tslint:disable-next-line:member-ordering
  selectableSetting: SelectableSettings = { mode: 'single', checkboxOnly: false };


  ngOnInit() {
    this.codeRuleSvc.getCodeRuleList().subscribe(datas => {
      if (datas && datas.length > 0) {
        datas.forEach(obj => {
          let baseInfo = new CodeRuleBaseInfo();
          baseInfo.init(obj);
          this.data.push(baseInfo);
        })
        this.gridData = process(this.data, this.state);
      }
    });
  }

  // 选中编号规则后的事件处理
  onSelectionChange(event) {
    if (event.selectedRows.length > 0) {
      this.codeRuleSvc.codeRule = null;
      this.codeRuleSvc.codeRule = event.selectedRows[0].dataItem;
    }
  }
}
